import { Component, OnInit } from '@angular/core';
import { Router } from "@angular/router";
import { CoreRoutes, RoomsPageName } from "@core/configs/routing-config";
import { RoomsMapService } from "@core/services/rooms-map.service";
import { AbstractAnimatedComponent } from "@shared/components/abstract-animated/abstract-animated.component";
import { AnimationBuilder } from "@angular/animations";
import { IRoom, IRoomPage } from "@shared/models/room-page.model";
import { DataService } from "@core/services/data.service";
import { IUser } from "@shared/models/user.model";
import { UtilityService } from "@core/utilities/utility.service";

@Component({
  selector: 'app-rooms-map-page',
  templateUrl: './rooms-map-page.component.html',
  styleUrls: ['./rooms-map-page.component.scss'],
})
export class RoomsMapPageComponent extends AbstractAnimatedComponent implements OnInit {

  isShowConfirmDialog: boolean = false;
  isConfirmOrder: boolean = false;
  selectedRoomMap: IRoom;
  selectedRoom: IRoom;
  pageData: IRoomPage;
  userData: IUser;

  constructor(
      private builder: AnimationBuilder,
      private _roomsMapService: RoomsMapService,
      private router: Router,
      private _dataService: DataService,
      private utilityService: UtilityService
  ) {
    super(builder);
  }

  ngOnInit() {
    this.pageData = this._dataService.getRoomData(RoomsPageName.ROOMS_MAP);
    this.userData = this._dataService.pagesData.user;
    this.selectedRoom = this.pageData.rooms[0];
    this.subscribes();
  }

  subscribes(): void {
    this._roomsMapService.isConfirmOrder$.subscribe(value => this.isConfirmOrder = value);
    this._roomsMapService.isShowConfirmDialog$.subscribe(value => this.isShowConfirmDialog = value);
  }

  closeConfirmDialog(): void {
    this._roomsMapService.isShowConfirmDialog$.next(false);
  }

  confirmOrder(): void {
    this._roomsMapService.isConfirmOrder$.next(true);
    this.closeConfirmDialog();
    this.router.navigate(['/' + CoreRoutes.COMPLETED]);
  }

  onOpenMobileRoomMap(room): void {
    this.selectedRoomMap = room;
    this._roomsMapService.isShowMobileMapDialog$.next(true);
  }

  onCloseMobileRoomMap(): void {
    this.selectedRoomMap = null;
    this._roomsMapService.isShowMobileMapDialog$.next(false);
  }

  get roomPrice() {
    return this.utilityService.formatPriceToNum(this.userData.price);
  }
}
