import { Component, OnInit } from '@angular/core';
import { AbstractAnimatedComponent } from "@shared/components/abstract-animated/abstract-animated.component";
import { AnimationBuilder } from "@angular/animations";
import { IRoomPage } from "@shared/models/room-page.model";
import { RoomsPageName } from "@core/configs/routing-config";
import { DataService } from "@core/services/data.service";

@Component({
  selector: 'app-dreams-location-page',
  templateUrl: './dreams-location-page.component.html',
  styleUrls: ['./dreams-location-page.component.scss']
})
export class DreamsLocationPageComponent extends AbstractAnimatedComponent implements OnInit {

  public selectedSweetDreamsLocation: any;
  pageData: IRoomPage;
  pagesNameListLength: number;
  pageStepIndex: number;

  constructor(
      private builder: AnimationBuilder,
      private _dataService: DataService
  ) {
    super(builder);
  }

  ngOnInit() {
    this.pageData = this._dataService.getRoomData(RoomsPageName.DREAMS_LOCATION);
    this.pageStepIndex = this._dataService.getPageStepIndex(RoomsPageName.DREAMS_LOCATION);
    this.pagesNameListLength = this._dataService.pagesNameList.length - 1;
  }
}
