import { Component, OnInit } from '@angular/core';
import { CoreRoutes } from "@core/configs/routing-config";
import { NavigationEnd, Router} from "@angular/router";
import { RoomsMapService } from "@core/services/rooms-map.service";
import { AbstractAnimatedComponent } from "@shared/components/abstract-animated/abstract-animated.component";
import { AnimationBuilder } from "@angular/animations";
import { DataService } from "@core/services/data.service";

@Component({
  selector: 'app-steps-page',
  templateUrl: './steps-page.component.html',
  styleUrls: ['./steps-page.component.scss']
})
export class StepsPageComponent extends AbstractAnimatedComponent implements OnInit {

  roomStepsList: Array<string>;
  currentRoomStepIndex: number;
  nextRoomStep: string;
  prevRoomStep: string;
  public coreRoutes = CoreRoutes;
  public isShowConfirmDialog: boolean = false;
  public isShowMobileMapDialog: boolean = false;

  constructor(
      private builder: AnimationBuilder,
      private router: Router,
      private _roomsMapService: RoomsMapService,
      private _dataService: DataService
  ) {
    super(builder);
  }

  ngOnInit() {
    this.router.events.subscribe(val => {
      if (val instanceof NavigationEnd) {
        this.setRouterParams();
      }
    });

    this.setRouterParams();
    this.subscribes();
  }

  subscribes(): void {
    this._roomsMapService.isShowConfirmDialog$.subscribe(value => this.isShowConfirmDialog = value);
    this._roomsMapService.isShowMobileMapDialog$.subscribe(value => this.isShowMobileMapDialog = value);
  }

  setRouterParams(): void {
    let urls = this.router.url.split('/');
    let currentRoomStep = urls[urls.length - 1];

    this.roomStepsList = this._dataService.pagesNameList;
    this.currentRoomStepIndex = this.roomStepsList.indexOf(currentRoomStep);
    this.nextRoomStep = this.roomStepsList[this.currentRoomStepIndex + 1];
    this.prevRoomStep = this.roomStepsList[this.currentRoomStepIndex - 1];
  }

  openConfirmDialog(): void {
    this._roomsMapService.isShowConfirmDialog$.next(true);
  }
}
