import { RouterModule, Routes } from "@angular/router";
import { CoreRoutes, LayoutRoutes, RoomsPageName } from "@core/configs/routing-config";
import { StepsPageComponent } from "./pages/steps-page/steps-page.component";
import { NgModule } from "@angular/core";
import { RoomLayoutComponent } from "./room-layout/room-layout.component";
import { AnimationGuard } from "@core/guards/animation.guard";
import { FloorPageComponent } from "./pages/steps-page/floor-page/floor-page.component";
import { ViewPageComponent } from "./pages/steps-page/view-page/view-page.component";
import { BathPageComponent } from "./pages/steps-page/bath-page/bath-page.component";
import { DreamsLocationPageComponent } from "./pages/steps-page/dreams-location-page/dreams-location-page.component";
import { RoomsMapPageComponent } from "./pages/steps-page/rooms-map-page/rooms-map-page.component";

const routes: Routes = [
  {
    path: LayoutRoutes.ROOMS,
    component: RoomLayoutComponent,
    canDeactivate: [AnimationGuard],
    children: [
      {
        path: CoreRoutes.ROOM,
        component: StepsPageComponent,
        canDeactivate: [AnimationGuard],
        children: [
          {
            path: RoomsPageName.FLOOR,
            component: FloorPageComponent,
            canDeactivate: [AnimationGuard]
          },
          {
            path: RoomsPageName.VIEW,
            component: ViewPageComponent,
            canDeactivate: [AnimationGuard]
          },
          {
            path: RoomsPageName.BATHE,
            component: BathPageComponent,
            canDeactivate: [AnimationGuard]
          },
          {
            path: RoomsPageName.DREAMS_LOCATION,
            component: DreamsLocationPageComponent,
            canDeactivate: [AnimationGuard]
          },
          {
            path: RoomsPageName.ROOMS_MAP,
            component: RoomsMapPageComponent,
            canDeactivate: [AnimationGuard]
          },
        ]
      }
    ]
  }
];


@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class RoomsRoutingModule {}
